body {
  background-color: #f5f5f5;
}

.project-list {
  width: 80%;
  margin: 0 auto;
  display: grid;
  // grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-template-columns: 1fr;
  gap: 1.25rem;
  margin-top: 1.5rem;
}

.project {
  // width: 20rem;
  width: 100%;
  height: 13rem;
  // border: 1px solid black;
  display: grid;
  grid-template-columns: auto 20rem;
  border-radius: 1rem;
  background-color: #fffefe;
  box-shadow: 0.2rem 0.3rem 1rem 0.5rem rgba(0, 0, 0, 0.352);
  margin-bottom: 1.5rem;
  text-align: left;

  div {
    border-top-right-radius: 10%;
    border-top-left-radius: 10%;
  }

  img {
    width: 100%;
    height: 13rem;
    object-fit: cover;
    object-position: 0% 0%;
    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem;
    left: 0;
  }

  h3 {
    width: 80%;
    margin: 1rem 0;
  }
  
  a {
    text-decoration: none;
  }

  button {
    border: 1px solid lightgrey;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    margin: 0;
    cursor: pointer;
    background-color: #fffefe;

    p {
      font-size: 1rem;
      margin: 0;
    }
  }

  button:hover {
    background-color: #f5f5f5;
  }
  
  .gh-logo {
    width: 1.5rem;
    height: auto;
    margin: 0.2rem;
  }
}

.project-wrapper {
  width: 95%;
  margin: 0 auto;
  align-items: left;
  align-content: left;
}

.project-text {
  width: 95%;
  margin: 0.7rem 0;
  // margin-top: 0.7rem;
}

.list {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: start;
  gap: 2rem;
  padding-left: 0px;
  width: 90%;
  margin: 0;
  margin-bottom: 1rem;
}

.tech {
  font-size: 0.8rem;
}

.project-link {
  color: black;
  text-decoration: none;
}
